/* ============================================
   LANDING PAGE STYLES
   ============================================ */

.landing-page {
  width: 100%;
  overflow-x: hidden;
  position: relative;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

/* Prevent horizontal scroll on mobile */
* {
  max-width: 100%;
  box-sizing: border-box;
}

/* Base mobile improvements */
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

/* Improve touch targets for all interactive elements */
button,
a,
input[type="button"],
input[type="submit"],
input[type="checkbox"],
input[type="radio"],
select {
  min-height: 44px;
  min-width: 44px;
  touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(201, 169, 97, 0.2);
}

img, video, iframe {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Ensure images don't overflow on any device */
img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

/* Prevent horizontal scroll on all devices */
* {
  max-width: 100%;
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
  width: 100%;
  position: relative;
}

/* ============================================
   STICKY NAVIGATION
   ============================================ */

.sticky-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 0);
  backdrop-filter: blur(0px);
  transition: all 0.3s ease;
  padding: 1rem 0;
}

.sticky-nav.scrolled {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.nav-container {
  max-width: 1280px; /* max-w-7xl equivalent */
  margin: 0 auto;
  padding: 1rem 1rem;
  position: relative;
}

@media (min-width: 640px) {
  .nav-container {
    padding: 1rem 1.5rem;
  }
}

@media (min-width: 1024px) {
  .nav-container {
    padding: 1rem 2rem;
  }
}

@media (min-width: 1280px) {
  .nav-container {
    padding: 1rem 3rem;
  }
}

.nav-center-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
}

.nav-cta-desktop {
  position: absolute;
  right: 0;
  display: none;
}

@media (min-width: 1024px) {
  .nav-cta-desktop {
    display: flex;
    align-items: center;
  }
}

.nav-logo {
  font-size: 1.25rem;
  font-weight: 800;
  color: #1a202c;
  transition: color 0.3s ease;
  letter-spacing: -1px;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
  font-family: 'Playfair Display', serif;
  line-height: 1.15;
}

.nav-logo sup {
  font-size: 0.6em;
  vertical-align: super;
  font-weight: 400;
  margin-left: 2px;
}

.nav-logo.scrolled {
  color: #1a202c;
}

@media (min-width: 768px) {
  .nav-logo {
    font-size: 1.75rem;
    letter-spacing: -1px;
  }
}

@media (min-width: 1024px) {
  .nav-logo {
    font-size: 2.25rem;
    font-weight: 800;
    letter-spacing: -1px;
  }
}

.sticky-nav:not(.scrolled) .nav-logo {
  color: white;
}

.sticky-nav:not(.scrolled) .nav-logo:hover {
  color: rgba(255, 255, 255, 0.9);
}

.nav-logo:hover {
  color: #c9a961;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.nav-links button {
  background: none;
  border: none;
  color: #2d3748;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.sticky-nav:not(.scrolled) .nav-links button {
  color: white;
}

.nav-links button:hover {
  background: rgba(0, 0, 0, 0.05);
  transform: translateY(-2px);
}

.nav-cta {
  background: linear-gradient(135deg, #c9a961, #d4af37) !important;
  color: white !important;
  padding: 0.5rem 1rem !important;
  font-size: 0.875rem !important;
  font-weight: 600 !important;
  border-radius: 8px !important;
  white-space: nowrap !important;
  transition: all 0.3s ease !important;
  box-shadow: 0 2px 8px rgba(201, 169, 97, 0.3) !important;
  border: none !important;
  cursor: pointer !important;
}

.nav-cta:hover {
  transform: translateY(-1px) scale(1.05);
  box-shadow: 0 4px 16px rgba(201, 169, 97, 0.4) !important;
  background: linear-gradient(135deg, #d4af37, #c9a961) !important;
}

.nav-cta:active {
  transform: translateY(0) scale(0.95);
}

/* Mobile Menu Toggle */
.mobile-menu-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  z-index: 10002;
  color: #1a202c;
  transition: color 0.3s ease;
  min-width: 44px;
  min-height: 44px;
  touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(201, 169, 97, 0.2);
}

.mobile-menu-toggle.scrolled {
  color: #1a202c;
}

.sticky-nav:not(.scrolled) .mobile-menu-toggle {
  color: white;
}

@media (min-width: 1024px) {
  .mobile-menu-toggle {
    display: none;
  }
}

.mobile-menu-toggle:hover {
  color: #c9a961;
}

/* Sidebar Navigation Styles */
.sidebar-nav-wrapper {
  display: none;
}

@media (min-width: 1024px) {
  .sidebar-nav-wrapper {
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 40;
    height: auto;
  }

  .sidebar-nav {
    background: #000000;
    box-shadow: 
      0 8px 32px rgba(0, 0, 0, 0.3),
      0 2px 8px rgba(0, 0, 0, 0.2),
      inset 0 1px 0 rgba(255, 255, 255, 0.05);
    border-right: 1px solid rgba(201, 169, 97, 0.15);
    border-radius: 0 16px 16px 0;
    padding: 0.75rem;
    min-width: 125px;
    max-width: 125px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .sidebar-nav:hover {
    box-shadow: 
      0 12px 40px rgba(0, 0, 0, 0.5),
      0 4px 12px rgba(0, 0, 0, 0.35),
      inset 0 1px 0 rgba(255, 255, 255, 0.08);
    border-right-color: rgba(201, 169, 97, 0.3);
  }

  .sidebar-nav-content {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
  }
  
  .sidebar-nav-content > div {
    display: flex;
    flex-direction: column;
  }

  .sidebar-button {
    position: relative;
    width: 100%;
    text-align: left;
    padding: 0.5rem 0.5rem;
    border-radius: 12px;
    background: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
  }

  .sidebar-button::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 3px;
    background: linear-gradient(180deg, #c9a961 0%, #d4af37 100%);
    transform: scaleY(0);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 0 2px 2px 0;
    box-shadow: 0 0 8px rgba(201, 169, 97, 0.5);
  }

  .sidebar-button:hover::before,
  .sidebar-button.active::before {
    transform: scaleY(1);
  }

  .sidebar-button.active {
    background: linear-gradient(135deg, rgba(201, 169, 97, 0.95) 0%, rgba(212, 175, 55, 0.95) 100%);
    box-shadow: 
      0 4px 12px rgba(201, 169, 97, 0.4),
      inset 0 1px 0 rgba(255, 255, 255, 0.2),
      0 0 20px rgba(201, 169, 97, 0.2);
    transform: translateX(2px);
  }

  .sidebar-button-text {
    font-size: 0.7rem;
    font-weight: 600;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    transition: all 0.3s ease;
    position: relative;
    z-index: 10;
    color: rgba(255, 255, 255, 0.85);
    line-height: 1.3;
    word-wrap: break-word;
    white-space: normal;
    display: block;
  }

  /* When there are multiple text lines, use flexbox */
  .sidebar-button-text .sidebar-text-line {
    display: block;
    line-height: 1.2;
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .sidebar-button-text .sidebar-text-line:not(:last-child) {
    margin-bottom: 0.1rem;
  }

  /* Apply flexbox when text lines exist (for multi-line titles) */
  .sidebar-button-text:has(> .sidebar-text-line) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .sidebar-button.active .sidebar-button-text {
    color: white;
  }

  .sidebar-button:not(.active):hover .sidebar-button-text {
    color: rgba(255, 255, 255, 0.95);
  }

  .sidebar-button:not(.active) {
    color: rgba(255, 255, 255, 0.85);
  }

  .sidebar-button:not(.active):hover {
    background: linear-gradient(135deg, rgba(201, 169, 97, 0.15) 0%, rgba(201, 169, 97, 0.08) 100%);
    transform: translateX(3px);
  }

  .sidebar-divider {
    height: 1px;
    background: linear-gradient(90deg, transparent 0%, rgba(201, 169, 97, 0.15) 50%, transparent 100%);
    transition: all 0.3s ease;
    margin: 0.25rem 0.5rem;
  }

  .sidebar-divider.active {
    background: linear-gradient(90deg, transparent 0%, rgba(201, 169, 97, 0.4) 50%, transparent 100%);
    height: 1.5px;
    box-shadow: 0 0 4px rgba(201, 169, 97, 0.3);
  }
}

