    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  }

  .sticky-nav:not(.scrolled) .close-text {
    color: #c9a961;
  }

  .hamburger.open {
    position: relative;
  }

  /* Hide the animated spans when open, show only the X pseudo-element */
  .hamburger.open span {
    opacity: 0;
  }

  .hamburger.open::after {
    content: '✕';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.5rem;
    color: #c9a961;
    font-weight: 700;
    z-index: 1;
  }

