   ============================================ */

.mortgage-calculator {
  background: #f7fafc;
  padding: 6rem 0;
}

.calculator-wrapper {
  max-width: 1000px;
  margin: 3rem auto 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
  width: 100%;
}

.calculator-inputs {
  background: white;
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.calc-input-group {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.calc-input-group label {
  font-weight: 600;
  color: #2d3748;
  font-size: 1rem;
}

.input-help-text {
  font-size: 0.85rem;
  color: #718096;
  font-style: italic;
  margin-top: -0.5rem;
}

/* Address Autocomplete Styles */
.address-autocomplete-wrapper {
  position: relative;
  width: 100%;
}

.address-suggestions {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border: 2px solid #c9a961;
  border-radius: 10px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  margin-top: 0.25rem;
  max-height: 300px;
  overflow-y: auto;
}

.address-suggestion-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  width: 100%;
  padding: 1rem;
  background: white;
  border: none;
  border-bottom: 1px solid #e2e8f0;
  text-align: left;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.95rem;
  color: #2d3748;
}

.address-suggestion-item:last-child {
  border-bottom: none;
}

.address-suggestion-item:hover {
  background: #f7fafc;
  color: #c9a961;
}

.address-suggestion-item svg {
  color: #c9a961;
  flex-shrink: 0;
}

.address-confirmed {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 0.5rem;
  padding: 0.5rem 0.75rem;
  background: rgba(201, 169, 97, 0.1);
  border: 1px solid rgba(201, 169, 97, 0.3);
  border-radius: 8px;
  font-size: 0.85rem;
  color: #2d3748;
  font-weight: 500;
}

.address-confirmed svg {
  color: #c9a961;
  flex-shrink: 0;
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.input-prefix {
  position: absolute;
  left: 1rem;
  color: #718096;
  font-weight: 600;
  z-index: 1;
}

.input-suffix {
  position: absolute;
  right: 1rem;
  color: #718096;
  font-weight: 600;
  z-index: 1;
}

.calc-input {
  width: 100%;
  padding: 1rem;
  padding-left: 2.5rem;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  color: #2d3748;
  transition: all 0.2s ease;
  font-family: inherit;
}

.calc-input:focus {
  outline: none;
  border-color: #c9a961;
  box-shadow: 0 0 0 3px rgba(201, 169, 97, 0.15);
}

.calc-input[type="number"] {
  -moz-appearance: textfield;
}

.calc-input[type="number"]::-webkit-outer-spin-button,
.calc-input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.select-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
}

.calc-select {
  width: 100%;
  padding: 1rem;
  padding-right: 3rem;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
