   ============================================ */

.buyer-quiz {
  background: linear-gradient(to bottom, #f7fafc 0%, #ffffff 100%);
  padding: 6rem 0;
  position: relative;
}

.quiz-container {
  max-width: 800px;
  margin: 3rem auto 0;
}

.quiz-progress {
  margin-bottom: 2rem;
}

.quiz-progress-bar {
  width: 100%;
  height: 8px;
  background: #e2e8f0;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 0.75rem;
}

.quiz-progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #c9a961, #d4af37);
  border-radius: 4px;
  transition: width 0.5s ease;
}

.quiz-progress-text {
  text-align: center;
  font-size: 0.95rem;
  color: #718096;
  font-weight: 600;
}

.quiz-question-card {
  background: white;
  padding: 3rem;
  border-radius: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08), 0 4px 16px rgba(0, 0, 0, 0.04);
  border: 2px solid rgba(201, 169, 97, 0.1);
  transition: all 0.3s ease;
}

.quiz-question-card:hover {
  box-shadow: 0 12px 40px rgba(201, 169, 97, 0.15), 0 6px 20px rgba(0, 0, 0, 0.06);
  border-color: rgba(201, 169, 97, 0.3);
}

.quiz-question-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 2rem;
  line-height: 1.4;
}

.quiz-hint-section {
  position: relative;
  margin-bottom: 2rem;
}

.quiz-hint-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: rgba(201, 169, 97, 0.1);
  border: 2px solid rgba(201, 169, 97, 0.3);
  border-radius: 12px;
  color: #c9a961;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.quiz-hint-button:hover {
  background: rgba(201, 169, 97, 0.2);
  border-color: rgba(201, 169, 97, 0.5);
  transform: translateY(-2px);
}

.quiz-hint-popup {
  position: absolute;
  top: calc(100% + 12px);
  left: 0;
  right: 0;
  background: #1a202c;
  color: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  border: 2px solid #c9a961;
  animation: slideDown 0.3s ease;
}

.quiz-hint-popup p {
  margin: 0;
  line-height: 1.6;
  font-size: 0.95rem;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.quiz-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.quiz-option {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.25rem 1.5rem;
  background: white;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  text-align: left;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  color: #2d3748;
  position: relative;
}

.quiz-option:hover:not(:disabled) {
  border-color: #c9a961;
  background: rgba(201, 169, 97, 0.05);
  transform: translateX(4px);
}

.quiz-option.selected {
  border-color: #c9a961;
  background: rgba(201, 169, 97, 0.1);
}

.quiz-option.correct {
  border-color: #48bb78;
  background: rgba(72, 187, 120, 0.1);
}

.quiz-option.incorrect {
  border-color: #f56565;
  background: rgba(245, 101, 101, 0.1);
}

.quiz-option:disabled {
  cursor: not-allowed;
  opacity: 0.8;
}

.quiz-option-letter {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 8px;
  background: #f7fafc;
  color: #2d3748;
  font-weight: 700;
  font-size: 1.1rem;
  flex-shrink: 0;
}

.quiz-option.selected .quiz-option-letter {
  background: #c9a961;
  color: white;
}

.quiz-option.correct .quiz-option-letter {
  background: #48bb78;
  color: white;
}

.quiz-option.incorrect .quiz-option-letter {
  background: #f56565;
  color: white;
}

.quiz-option-text {
  flex: 1;
  line-height: 1.5;
}

.quiz-option-check,
.quiz-option-x {
  position: absolute;
  right: 1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.quiz-option-check {
  color: #48bb78;
}

.quiz-option-x {
  color: #f56565;
}

.quiz-results {
  max-width: 800px;
  margin: 3rem auto 0;
}

.quiz-results-card {
  background: white;
  padding: 3rem;
  border-radius: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08), 0 4px 16px rgba(0, 0, 0, 0.04);
  border: 2px solid rgba(201, 169, 97, 0.1);
}

.quiz-results-title {
  font-size: 2rem;
  font-weight: 700;
  color: #2d3748;
  text-align: center;
  margin-bottom: 2rem;
}

.quiz-score-display {
  text-align: center;
  margin-bottom: 3rem;
}

.quiz-score-circle {
  display: inline-flex;
