  margin-bottom: 1rem;
}

.quiz-score-number {
  font-size: 4rem;
  font-weight: 800;
  color: #c9a961;
  line-height: 1;
}

.quiz-score-total {
  font-size: 2rem;
  font-weight: 600;
  color: #718096;
}

.quiz-score-percentage {
  font-size: 1.25rem;
  font-weight: 600;
  color: #4a5568;
}

.quiz-results-breakdown {
  margin-bottom: 2rem;
}

.quiz-results-breakdown h4 {
  font-size: 1.25rem;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 1.5rem;
}

.quiz-result-item {
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 1rem;
  border: 2px solid #e2e8f0;
  background: #f7fafc;
}

.quiz-result-item.correct {
  border-color: #48bb78;
  background: rgba(72, 187, 120, 0.05);
}

.quiz-result-item.incorrect {
  border-color: #f56565;
