/* Consultation Includes Section */
.consultation-includes {
  background: linear-gradient(135deg, rgba(201, 169, 97, 0.25), rgba(212, 175, 55, 0.18), rgba(255, 215, 0, 0.1));
  border: 4px solid rgba(201, 169, 97, 0.5);
  border-radius: 28px;
  padding: 3.5rem;
  margin: 3rem 0;
  max-width: 950px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 12px 48px rgba(201, 169, 97, 0.25), 0 6px 24px rgba(212, 175, 55, 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  position: relative;
  overflow: hidden;
}

.consultation-includes::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: linear-gradient(90deg, #c9a961, #d4af37, #ffd700, #d4af37, #c9a961);
  background-size: 200% 100%;
  animation: shimmer 3s ease-in-out infinite;
}

.consultation-includes::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.15) 0%, transparent 70%);
  animation: rotate 20s linear infinite;
  pointer-events: none;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes shimmer {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.includes-title {
  font-size: 1.85rem;
  font-weight: 800;
  background: linear-gradient(135deg, #c9a961, #d4af37);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 2.5rem;
  text-align: center;
  text-shadow: 0 2px 8px rgba(201, 169, 97, 0.3);
  position: relative;
  z-index: 2;
}

.includes-content {
  position: relative;
  z-index: 1;
}

.includes-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 1.75rem;
}

.includes-item {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  font-size: 1.15rem;
  line-height: 1.75;
  color: #2d3748;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  border-left: 5px solid #c9a961;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 16px rgba(201, 169, 97, 0.15), 0 2px 8px rgba(0, 0, 0, 0.05);
}

.includes-item:hover {
  background: rgba(255, 255, 255, 1);
  transform: translateX(10px) translateY(-2px);
  box-shadow: 0 8px 24px rgba(201, 169, 97, 0.3), 0 4px 12px rgba(212, 175, 55, 0.2);
  border-left-width: 6px;
}

.includes-item.highlight-item {
  background: linear-gradient(135deg, rgba(201, 169, 97, 0.2), rgba(212, 175, 55, 0.15), rgba(255, 255, 255, 0.9));
  border-left-color: #d4af37;
  border-left-width: 6px;
  font-weight: 500;
  box-shadow: 0 6px 20px rgba(201, 169, 97, 0.25), 0 3px 10px rgba(212, 175, 55, 0.15);
}

.includes-item.highlight-item:hover {
  background: linear-gradient(135deg, rgba(201, 169, 97, 0.3), rgba(212, 175, 55, 0.2), rgba(255, 255, 255, 0.95));
  transform: translateX(10px) translateY(-3px) scale(1.02);
  box-shadow: 0 10px 32px rgba(201, 169, 97, 0.35), 0 5px 16px rgba(212, 175, 55, 0.25);
}

.includes-icon {
  display: flex;
