.contact-form {
  max-width: 800px;
  margin: 3rem auto 0;
  background: white;
  padding: 3.5rem 4rem;
  border-radius: 32px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2), 0 8px 24px rgba(0, 0, 0, 0.15);
