  color: #2d3748;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  font-weight: 500;
}

.form-privacy {
  text-align: center;
  font-size: 0.85rem;
  color: #718096;
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e2e8f0;
}

.form-required-note {
