}

.cta-button.submitting {
  background: #48bb78;
  pointer-events: none;
}

.contact-form .cta-button {
  width: 100%;
  margin-top: 2rem;
  padding: 1.25rem 2rem;
  font-size: 1.1rem;
  font-weight: 700;
  min-height: 56px;
  box-shadow: 0 8px 24px rgba(201, 169, 97, 0.3);
  transition: all 0.3s ease;
}

.contact-form .cta-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(201, 169, 97, 0.4);
}

.contact-form .cta-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* ============================================
   SUCCESS MODAL
   ============================================ */

.success-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: fadeIn 0.3s ease;
  backdrop-filter: blur(4px);
}

.success-modal {
  background: white;
  padding: 3rem;
  border-radius: 24px;
  max-width: 500px;
  width: 90%;
  text-align: center;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.3s ease;
  position: relative;
}

.success-icon {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, #48bb78, #38a169);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: white;
  margin: 0 auto 1.5rem;
  animation: scaleIn 0.4s ease;
}

.success-modal h3 {
  font-size: 2rem;
  color: #2d3748;
  margin-bottom: 1rem;
  font-weight: 700;
}

.success-modal p {
  font-size: 1.1rem;
  color: #718096;
  line-height: 1.6;
  margin-bottom: 2rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

/* ============================================
   TESTIMONIALS SECTION
   ============================================ */

.testimonials {
  background: linear-gradient(to bottom, #f7fafc 0%, #ffffff 100%);
  padding: 6rem 0;
  position: relative;
}

.testimonials::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(to right, transparent, rgba(201, 169, 97, 0.3), transparent);
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.testimonial-card {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.testimonial-card::before {
