/* ============================================
   FAQ SECTION
   ============================================ */

.faq {
  background: #f7fafc;
  padding: 6rem 0;
}

.faq-list {
  max-width: 900px;
  margin: 3rem auto 0;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.faq-bar {
  background: white;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  overflow: hidden;
  transition: all 0.3s ease;
}

.faq-bar:hover {
  border-color: #c9a961;
}

.faq-bar.open {
  border-color: #c9a961;
  box-shadow: 0 2px 8px rgba(201, 169, 97, 0.1);
}

.faq-question-bar {
  width: 100%;
