  border: none;
  text-align: left;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 1rem;
  font-weight: 600;
  color: #2d3748;
  gap: 1rem;
}

.faq-question-bar:hover {
  background: #f7fafc;
  color: #c9a961;
}

.faq-question-bar span {
  flex: 1;
  line-height: 1.5;
}

.faq-icon {
