    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .roi-card {
    padding: 2rem 1.5rem;
  }

  .roi-amount {
    font-size: 2rem;
  }

  .comparison-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .rent-vs-buy-comparison {
    padding: 2rem 1.5rem;
    margin: 3rem 0;
  }

  .comparison-title {
    font-size: 1.5rem;
  }

  .comparison-subtitle {
    font-size: 1rem;
  }

  .comparison-amount {
    font-size: 1.75rem;
  }

  .family-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .stat-card {
    padding: 1.5rem;
  }

  .stat-number {
    font-size: 1.5rem;
  }

  .consultation-includes {
    padding: 2rem 1.5rem;
    border-radius: 20px;
  }

  .includes-list {
    gap: 1rem;
  }

  .includes-item {
    padding: 1rem;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }

  .tax-benefits-callout {
    flex-direction: column;
    padding: 2rem 1.5rem;
    gap: 1.5rem;
  }

  .tax-icon {
    align-self: center;
  }
}

/* Large screen optimizations */
@media screen and (min-width: 1920px) {
  .floating-cta {
    bottom: 3rem;
    right: 3rem;
    padding: 1.5rem 3rem;
    font-size: 1.2rem;
  }

  .back-to-top {
    bottom: 3rem;
    left: 3rem;
    width: 60px;
    height: 60px;
    font-size: 1.8rem;
  }
}

/* Projector/Ultra-wide optimizations */
@media screen and (min-width: 2560px) {
  .floating-cta {
    bottom: 4rem;
    right: 4rem;
    padding: 1.75rem 3.5rem;
    font-size: 1.3rem;
  }

  .back-to-top {
    bottom: 4rem;
    left: 4rem;
    width: 70px;
    height: 70px;
    font-size: 2rem;
  }
}
